<template>
  <div class="main">
    <!-- <div class="title">ChatGPT 聊天记录生成器</div>-->
    <el-dialog
      v-model="imgVisible"
      fullscreen
      center
      title="截图预览"
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :append-to-body="true"
    >
      <el-scrollbar height="100%">
        <img :src="imgUrl" style="width: 100%" />
      </el-scrollbar>
      <template #footer>
        <span> 右击/长按图片保存 </span>
      </template>
    </el-dialog>

    <el-dialog
      v-model="modifyVisible"
      width="90%"
      center
      align-center
      :title="'修改' + modifyTypeText"
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :append-to-body="true"
    >
      <el-scrollbar height="100%">
        <el-input
          v-model="modifiedContent"
          :rows="4"
          type="textarea"
          :placeholder="'请输入' + modifyTypeText"
          resize="none"
          @keyup.enter="modifyType == 'q' ? modifyContent() : true"
        />
      </el-scrollbar>
      <template #footer>
        <el-button type="info" @click="modifyVisible = false" size="large">
          取消
        </el-button>
        <el-button type="primary" @click="modifyContent" size="large"
          >确认</el-button
        >
      </template>
    </el-dialog>

    <div class="talks">
      <el-scrollbar height="100%">
        <div
          v-if="contents.length === 0"
          style="
            height: 100%;
            width: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            position: absolute;
          "
        >
          <el-empty description="未添加对话" />
        </div>

        <div id="talks-container">
          <Question
            v-for="(item, index) in contents"
            ref="question"
            :key="index"
            :index="index"
            :type="item.type"
            :content="item.content"
            :randomNumber="randomNumber"
            @deleteContent="deleteContent"
            @changeModifyIndex="changeModifyIndex"
          />
        </div>
      </el-scrollbar>
    </div>
    <el-input
      class="input"
      v-model="content"
      :rows="4"
      type="textarea"
      :placeholder="'请输入' + typeText + '，双击某问题/回答可修改/删除。'"
      resize="none"
      @keyup.enter="type == 'q' ? addContent() : true"
    />
    <div class="btns">
      <el-button type="success" @click="getImg" size="large"
        >生成截图</el-button
      >
      <el-button type="danger" @click="clearContent" size="large"
        >清空对话</el-button
      >
      <el-button type="primary" @click="addContent" size="large">{{
        '添加' + typeText
      }}</el-button>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'
import { ElMessage, ElMessageBox } from 'element-plus'
import Question from './Question.vue'

export default {
  name: 'MainPage',
  components: {
    Question,
  },
  data() {
    return {
      type: 'q',
      content: '',
      contents: [],
      imgUrl: '',
      imgVisible: false,
      randomNumber: 0,
      modifyVisible: false,
      modifiedContent: '',
      modifyIndex: -1,
    }
  },
  computed: {
    typeText() {
      return this.type === 'q' ? '问题' : '回答'
    },
    modifyType() {
      return this.modifyIndex === -1
        ? 'q'
        : this.contents[this.modifyIndex].type
    },
    modifyTypeText() {
      return this.modifyType === 'q' ? '问题' : '回答'
    },
  },
  methods: {
    clearContent() {
      if (this.contents.length === 0) {
        return
      }
      const that = this
      ElMessageBox.confirm('确认要清空所有对话？', '警告', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning',
        'close-on-click-modal': false,
        'close-on-press-escape': false,
      })
        .then(() => {
          that.contents = []
          that.type = 'q'
        })
        .catch(() => {})
    },
    changeModifyIndex(index) {
      this.modifyIndex = index
      this.modifiedContent = this.contents[index].content
      this.modifyVisible = true
    },
    deleteContent(index) {
      if (this.contents.length % 2) {
        this.type = this.type === 'q' ? 'a' : 'q'
      }
      this.contents.splice(index, 2)
    },
    modifyContent() {
      if (this.modifiedContent.trim().length === 0) {
        ElMessage({
          message: '内容不能为空',
          offset: 100,
          type: 'error',
          grouping: true,
          duration: 1000,
        })
        return
      }
      this.contents[this.modifyIndex].content = this.modifiedContent
      this.modifiedContent = ''
      this.modifyVisible = false
      this.modifyIndex = -1
    },
    addContent() {
      const that = this
      if (this.content.trim().length === 0) {
        ElMessage({
          message: '内容不能为空',
          offset: 100,
          type: 'error',
          grouping: true,
          duration: 1000,
        })
        return
      }
      this.contents.push({
        content: that.content,
        type: that.type,
      })
      this.type = this.type === 'q' ? 'a' : 'q'
      this.content = ''
    },
    getImg() {
      if (this.contents.length === 0) {
        //ElMessage.error('未添加对话')
        ElMessage({
          message: '未添加对话',
          offset: 100,
          type: 'error',
          grouping: true,
          duration: 1000,
        })
        return
      }

      this.$refs['question'].map((item) => {
        item.quickHideControlBtns()
      })

      const dom = document.getElementById('talks-container')
      const scale = window.devicePixelRatio

      // 传入节点原始宽高
      const width = dom.offsetWidth
      const height = dom.offsetHeight

      // html2canvas配置项
      const ops = {
        scale,
        width,
        height,
        useCORS: true,
        allowTaint: false,
      }

      html2canvas(dom, ops).then((canvas) => {
        this.imgUrl = canvas.toDataURL('image/jpg')
        this.imgVisible = true
      })
    },
  },
  mounted() {
    this.randomNumber = Math.floor(Math.random() * (71 - 1 + 1)) + 1
  },
}
</script>

